
declare var require: any
import { Component, OnInit } from '@angular/core';
//import truthAndDareFile from 'src/assets/truth.json'

const  truthAndDareFile = require('src/assets/truth.json')

@Component({
  selector: 'app-main-module',
  templateUrl: './main-module.component.html',
  styleUrls: ['./main-module.component.css']
})
export class MainModuleComponent implements OnInit {

truthQestions=this.shuffle(truthAndDareFile.truth)
dareQuesttions=this.shuffle(truthAndDareFile.dare)
truthCount=0;
dareCount=0;
state=""
  constructor() {

 

   }


  qText="How old were you when your parents sat you down for “the talk” and what did they say (or not say) about “the birds and the bees”  "

  ngOnInit() {
  }

  questionPhase =false;


  truth(){
    this.questionPhase=true;
    this.qText=this.truthQestions[this.truthCount]
    this.truthCount++;
    this.state="t"
  }

dare(){
  this.questionPhase=true;
  this.qText=this.dareQuesttions[this.dareCount]
  this.dareCount++;
  this.state="d"
}

done(){
  this.questionPhase=false;
}

next(){
  if(this.state=="t"){
    this.truth()
  }else if(this.state=="d"){
    this.dare()
  }
}



shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}
}
